import address from "../controllers/address";
export default (app) => {

    /* Routes for add address */
    app.route("/address/addAddress").post(address.addAddress);

    app.route("/address/getAllAddress").get(address.getAllAddress);

    app.route("/address/updateAddress").put(address.updateAddress);

    app.route("/address/deleteAddress/:idaddress").delete(address.deleteAddress);

    return app;
};